"use client";

import { useState } from "react";
import { ElevenLabsSpeech } from "@elevenlabs/react";

export default function CatTTS() {
  const [text, setText] = useState("");
  const [playing, setPlaying] = useState(false);

  const handlePlay = async () => {
    if (!text) return;
    setPlaying(true);
  };

  return (
    <div style={{ display: "flex", flexDirection: "column", gap: "10px" }}>
      <input
        type="text"
        placeholder="Type something for your cat..."
        value={text}
        onChange={(e) => setText(e.target.value)}
        style={{ padding: "8px", fontSize: "16px" }}
      />

      <ElevenLabsSpeech
        apiKey={process.env.NEXT_PUBLIC_ELEVENLABS_API_KEY}
        voice="XdflFrQO8wbGpWMNZHFr" // your voice ID
        text={text}
        onStart={() => setPlaying(true)}
        onEnd={() => setPlaying(false)}
        autoPlay={true}
      />

      <button onClick={handlePlay} disabled={playing || !text}>
        {playing ? "Playing..." : "Play Cat Voice"}
      </button>
    </div>
  );
}
